<template>
  <div>
    <el-table :data="tableData" style="width: 100%" fit>
      <el-table-column prop="date" label="">
      </el-table-column>
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column prop="address" label="">
      </el-table-column>
    </el-table>
    <div v-for="(item, index) in ['zhangsan', 'lisi']" :key="index">
      {{index}}{{item}}
    </div>
    <el-form>
      <el-form-item label="ssss" :required="fit">
        <el-input v-model="value"></el-input>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      fit: true,
      value: "",
      tableData: [
        {
          date: "2016-05-02",
          name: "",
          address: " 1518 "
        },
        {
          date: "2016-05-04",
          name: "",
          address: " 1517 "
        },
        {
          date: "2016-05-01",
          name: "",
          address: " 1519 "
        },
        {
          date: "2016-05-03",
          name: "",
          address: " 1516 "
        }
      ]
    };
  }
};
</script>



