<template>
  <el-dialog title="" width="600px" :visible.sync="is_visible" @close="on_close">
    <el-form label-width="120px" style="width: 500px">
      <el-form-item label=":">
        <el-select v-model="params.format" placeholder="" style="width: 100%">
          <el-option label="xls" value="xls"></el-option>
          <el-option label="csv" value="csv"></el-option>
          <el-option label="json" value="json"></el-option>
          <el-option label="html" value="html"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label=":">
        <el-input v-model="params.page_size" type="number" placeholder="1000"></el-input>
      </el-form-item>
      <el-form-item label=":">
        <el-input v-model="params.page" type="number"></el-input>
      </el-form-item>
      <el-form-item>
      <el-button v-loading="loading" type="primary" @click="on_submit" style="width: 100%">&nbsp;&nbsp;</el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>

<script>
  export default {
    data(){
      return {
        loading: false,
        is_visible: true,
        params: Object.assign({}, this.ps)
      }
    },
    props: ["ps", "url"],
    mounted(){
      // this.params.format = "xls"
      // this.params.page_size = 100
      // this.params.page = 1
    },
    methods:{
      on_submit(){
        var url = this.url + "/export"
        this.loading = true
        this.$c_master.get(url, this.params).then((response)=>{
          this.loading = false
          this.$message.success("")
          this.on_close()
        }).catch((error)=>{
          this.loading = false
        })
      },
      on_close(){
        this.$el.parentNode.removeChild(this.$el)
        this.$destroy()
      }
    }
  }
</script>



